.home {
    width: 100%;
}


.landing {
    height: 100vh;
    position: relative;
    text-align: center;
}

.landingBg {
    position: absolute;
    width: 100%;
    height: 98vh;
    background-image: url(/website/static/media/landingbg.4568e64d.jpg);
    background-size: cover;
    background-position-x: 50%;
    background-attachment: fixed;
    background-repeat: no-repeat;
}

.landingContent {
    padding: 80px 0 30px 0;   
    width: 100%;
    max-height: 370px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #fff;
}

.landingLogo {
    width: 550px;
}

.sloganContainer {
    border-bottom: 2px solid #08415c;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    max-width: 500px;
    padding: 0 40px;
    margin: 20px auto 25px auto;
}

.sloganContainer h1 {
    font-size: 32px;
    font-weight: 300;
    font-style: italic;
    color: #08415c;
    background-color: white;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 0 10px;
    margin: 0 auto -20px auto;
}

.scrollIndicator {
    position: -webkit-sticky;
    position: sticky;
    bottom: 50px;
    display: block;
    margin: 20px auto;
    color: #efc51f;
    text-align: center;
    transition: opacity 0.5s;
}

.scrollText {
    margin-top: 10px;
    font-size: 20px;
    transition: opacity 0.4s;
    color: rgb(200, 140, 0);
}

.scrollTextVisible {
    opacity: 1;
}

.scrollTextHidden {
    opacity: 0;
}

.scrollIndHidden {
    opacity: 0;
}

@media only screen and (max-width: 600px){
    .landingContent {
        padding: 100px 0 30px 0;
    }
    .landingLogo {
        width: 350px;
    }
    .sloganContainer {
        padding: 0 20px;
        border-bottom-width: 1px;
    }
    .sloganContainer h1 {
        font-size: 18px;
        margin-bottom: -10px;
    }
    .landingContent h4 {
        margin-top: 10px;
        font-size: 14px;
        font-weight: 600;
    }
}
.introBlock {
    position: relative;
    background-color: white;
    padding: 50px 0 0 0;
    margin-bottom: 30px;
}

.introAnchor {
    position: absolute;
    top: -60px;
}

.introInner {
    width: 90%;
    max-width: 900px;
    margin: 0 auto;
    text-align: center;
    border-bottom: 1px solid lightgray;
    padding-bottom: 35px;
}

.introInner h2 {
    font-size: 48px;
    font-weight: 200;
}

.introInner p {
