.infoGridWrapper {
    position: relative;
    background-color: white;
    padding-top: 20px;
    margin-bottom: 100px;
}

.infoGridAnchor {
    position: absolute;
    top: -60px;
}

.infoGrid {
    width: 1200px;
    margin: 0 auto 20px auto;
}

.infoGrid h3 {
    text-align: center;
    margin: 30px 0;
    font-size: 48px;
    font-weight: 200;
}

.infoBlocks {
    margin-top: 50px;
}

@media only screen and (min-width: 601px) and (max-width: 1330px){
    .infoGrid {
        width: 90vw;
    }
}

@media only screen and (max-width: 900px){
    .infoGrid h3 {
        font-size: 24px;
        font-weight: 500;
    }
}

@media only screen and (max-width: 600px){
    .infoGrid {
        width: 360px;
    }
}
.infoBlock {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 600px;
}

.infoSubBlock {
    box-sizing: border-box;
    width: 600px;
    height: 600px;
}

.ibText {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 10px;
    color: white;
}

.ibText h2 {
    margin: 0 0 20px 0;
    text-align: center;
    font-size: 30px;
    font-weight: 600;
    letter-spacing: 1px;
}

.ibText p {
    text-align: center;
    font-weight: 600;
    padding: 30px;
}

.ibImg {
    overflow: hidden;
}

.ibImg img {
    height: 100%;
}

@media only screen and (min-width: 601px) and (max-width: 1330px){
    .infoBlock {
        width: 90vw;
        height: 45vw;
        min-height: 45vw;
    }
    .infoSubBlock {
        height: auto;
        min-height: 45vw;
        width: 45vw;
    }
    .ibText h2 {
        font-size: 26px;
    }
    .ibText p {
        font-size: 16px;
    }
}

@media only screen and (max-width: 900px){
    .ibText h2 {
        font-size: 20px;
        margin: 30px 0 0 0;
    }
    .ibText p {
        font-size: 16px;
    }
}

@media only screen and (max-width: 600px){
    .infoBlock {
        grid-template-columns: 1fr;
        height: auto;
        min-height: 360px;
        width: 360px;
    }
    .infoSubBlock {
        height: auto;
        min-height: 360px;
        width: 360px;
    }
    .ibImg {
        height: 360px;
    }
    .ibImg img {
        height: 360px;
    }
}
.applyInfo {
    position: relative;
    padding-top: 50px;
    text-align: center;
}

.aiAnchor {
    position: absolute;
    top: 100px;
}

.aiUnderlined {
    width: 90%;
    max-width: 900px;
    margin: 0 auto 170px auto;
    border-bottom: 1px solid lightgray;
}

.aiUnderlined h2 {
    margin: 0 0 10px 0;
    font-size: 32px;
    font-weight: 500;
    color: #ebbab9;
}

.applyInfo h1 {
    font-size: 48px;
    font-weight: 500;
    margin-bottom: 80px;}

.aiCurve {
    margin-bottom: -10px;
}

.aiContentWrapper {
    background-color: rgb(100, 120, 200);
    padding-bottom: 160px;
}

.aiContent {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    justify-content: center;
    justify-items: center;
    margin: 0 auto;
    width: 75%;
    max-width: 1000px;
}

.aiTextBox {
    text-align: start;
    max-width: 450px;
}

.aiTextBox h2 {
    color: #fff;
    font-size: 48px;
    font-weight: 200;
}

.aiTextBox p {
    padding-bottom: 30px;
    font-size: 18px;
}

.aiBoldText {
    color: #fff;
    font-weight: 700;
}

.aiNormalText {
    color: #fff;
    opacity: 0.95;
    font-weight: 500;
}

.aiApplyWrapper {
    box-sizing: border-box;
    margin-top: 30px;
    padding: 25px 50px;
    background-color: white;
    max-width: 320px;
    align-self: center;
    text-decoration: none;
    text-align: left;
    font-weight: 600;
    line-height: 1.25em;
    
}

.aiApplyWrapper i {
    color: #08415c;
}


.buttonWrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 40px;
}


.aiToApply { 
    background-color: #efc51f;
    color: black; 
    font-size: 17px;
    text-decoration: none;
    box-shadow: 0px 3px 4px #ccc;
    display: block;
    padding: 20px;
    width: 100px;
    font-weight: 600;
    text-align: center; 
    margin-left: 20px;
    text-transform: uppercase;
}

@media only screen and (max-width: 600px) {
    .aiContentWrapper {
        padding-bottom: 50px;
    }
    .aiTextBox {
        box-sizing: border-box;
        max-width: 350px;
        padding: 0 17px;
    }
    .aiTextBox h2 {
        text-align: center;
        padding-bottom: 10px;
    }

    .aiTextBox p {
        font-size: 16px;
    }

    .aiUnderlined {
        margin: 0 auto 50px auto;
    }

}
.blogPreviewWrapper {
    position: relative;
    margin-bottom: 30px;
    padding: 40px 0;
}

.blogPreviewAnchor {
    position: absolute;
    top: -100px;
}

.blogPreviewTitle {
    text-align: center;
    font-size: 48px;
    font-weight: 200;
}

.blogPreview {
    display: grid;
    grid-template-columns: repeat(5, 180px);
    justify-content: center;
    justify-items: center;
    grid-gap: 10px;
    gap: 10px;
    max-width: 1000px;
    margin: 0 auto;
}

.blogPreview a {
    text-decoration: none;
}

.blogPreviewCard {
    box-sizing: border-box;
    display: block;
    width: 175px;
    height: 250px;
    text-align: center;
}

.bpcPost {
    background-color: #ccc;
    position: relative;
    transition: opacity 1s;
}

.bpcPost:hover {
    opacity: 0.7;
}

.bpcPost h4 {
    margin-bottom: 0;
    margin-top: 10px;
    font-size: 14px;
}

.bpcPost h3 {
    margin-top: 7px;
    font-size: 18px;
}

.bpcPost p {
    padding: 0 3px;
    margin: 5px;
    border-radius: 10px;
    position: absolute;
    bottom: 0;
    font-size: 15px;
}

.bpcTips {
    padding: 30px;
    color: white;
    background-color: #08415c;
}


.bpcLink {
    padding-top: 30px;
    color: white;
    background-color: black;
    transition: all 1s;
}

.bpcLink:hover {
    background-color: #4e4e4e;
}

@media only screen and (min-width: 601px) and (max-width: 1000px){
    .blogPreview {
        grid-template-columns: repeat(3, 180px)
    }
    .bpcLink {
        width: 365px;
        grid-column: 2 / 4;
    }
}

@media only screen and (max-width: 600px){
    .blogPreview {
        grid-template-columns: repeat(2, 180px);
        grid-column-gap: 0px;
        -webkit-column-gap: 0px;
                column-gap: 0px;
        grid-row-gap: 5px;
        row-gap: 5px;
    }
    .bpcLink {
        width: 355px;
        height: 200px;
        padding-top: 20px;
        grid-row: 3;
        grid-column: 1 / 3;
    }

    .blogPreviewWrapper {
        padding: 0;
    }

    .blogPreviewTitle {
        font-size: 24px;
        font-weight: 500;
        padding: 15px;
    }
}
